	module.exports = {"data":{"challengeNode":{"title":"Use @for to Create a Sass Loop","description":["你可以在 Sass 中使用<code>@for</code>循环，它的表现类似与 JavaScript 中的<code>for</code>循环。","<code>@for</code>以两种方式使用：\"start through end\" 或 \"start to end\"。主要区别在于“开始结束”<em> 排除 </em> 结束号码，而“开始结束”<em> 包括 </em> 结束号码。","这是一个开始 <b> 到 </b> 结束示例：","<blockquote>@for $i from 1 through 12 {<br>&nbsp;&nbsp;.col-#{$i} { width: 100%/12 * $i; }<br>}</blockquote>","<code>#{$i}</code>部分是将变量（<code>i</code>）与文本组合成字符串的语法。当 Sass 文件转换为 CSS 时，它看起来像这样：","<blockquote>.col-1 {<br>&nbsp;&nbsp;width: 8.33333%;<br>}<br><br>.col-2 {<br>&nbsp;&nbsp;width: 16.66667%;<br>}<br><br>......<br><br>.col-12 {<br>&nbsp;&nbsp;width: 100%;<br>}</blockquote>","这是创建网格布局的有效方法。现在，你有 12 个可用作 CSS classes 的列宽选项。","<hr>","编写<code>@for</code>指令，使<code>$j</code>的值为从 1（包含）到 6（不包含）。","它应该创建 5 个名为<code>.text-1</code>的 classes 到<code>.text-5</code>，其中每个 class 的<code>font-size</code>设置为 10px 乘以索引。"],"challengeType":0,"fields":{"slug":"/front-end-libraries/sass/use-for-to-create-a-sass-loop","blockName":"Sass","tests":[{"text":"你应使用<code>@for</code>指令。","testString":"assert(code.match(/@for /g), '你应使用<code>@for</code>指令。');"},{"text":"<code>.text-1</code>class 的<code>font-size</code>应为 10px。","testString":"assert($('.text-1').css('font-size') == '10px', '<code>.text-1</code>class 的<code>font-size</code>应为 10px。');"},{"text":"<code>.text-2</code>class 的<code>font-size</code>应为20px。","testString":"assert($('.text-2').css('font-size') == '20px', '<code>.text-2</code>class 的<code>font-size</code>应为20px。');"},{"text":"<code>.text-3</code>class 的<code>font-size</code>应为30px。","testString":"assert($('.text-3').css('font-size') == '30px', '<code>.text-3</code>class 的<code>font-size</code>应为30px。');"},{"text":"<code>.text-4</code>class 的<code>font-size</code>应为40px。","testString":"assert($('.text-4').css('font-size') == '40px', '<code>.text-4</code>class 的<code>font-size</code>应为40px。');"},{"text":"<code>.text-5</code>class 的<code>font-size</code>应为50px。","testString":"assert($('.text-5').css('font-size') == '50px', '<code>.text-5</code>class 的<code>font-size</code>应为50px。');"}]},"required":[{"link":null,"raw":true,"src":"https://cdnjs.cloudflare.com/ajax/libs/sass.js/0.10.9/sass.sync.min.js"}],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style type='text/sass'>\n  \n  \n  \n</style>\n\n<p class=\"text-1\">Hello</p>\n<p class=\"text-2\">Hello</p>\n<p class=\"text-3\">Hello</p>\n<p class=\"text-4\">Hello</p>\n<p class=\"text-5\">Hello</p>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[{"link":null,"raw":true,"src":"https://cdnjs.cloudflare.com/ajax/libs/sass.js/0.10.9/sass.sync.min.js"}],"nextChallengePath":"/front-end-libraries/sass/use-each-to-map-over-items-in-a-list","id":"587d7dbe367417b2b2512bb9"},"slug":"/front-end-libraries/sass/use-for-to-create-a-sass-loop"}}

